@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  position: relative;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
body {
  background: #FDFDFD;
  background-position: top center;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 2.2rem;
  font-weight: 400;
  color: #252525;
}

.page {
  position: relative;
}

.btn {
  color: #fff;
  background: #005DAB;
}

a {
  color: #252525;
}

.container {
  max-width: 108rem;
  padding: 0 1rem;
  margin: 0 auto;
}

.columns__inner {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}

.header {
  padding-top: 1rem;
  margin-bottom: 3rem;
}
.header__logo {
  max-width: 26rem;
}
.header__inner {
  margin: 0 1rem;
}
.header__main {
  display: flex;
  align-items: flex-end;
  gap: 3rem;
  margin-bottom: 2rem;
}
.header__top {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
}
.header__lang {
  display: flex;
  gap: 0.5rem;
  transform: translateY(25%);
}
.header__title {
  max-width: 59rem;
  text-transform: uppercase;
  color: #005DAB;
  font-size: 2.2rem;
  font-weight: 700;
}
.header__title span {
  text-transform: none;
  color: #005DAB;
  font-size: 1.6rem;
  font-weight: 40;
}
.header__form {
  position: relative;
  border-bottom: 1px solid #005DAB;
}
.header__form-search {
  width: 14rem;
  padding-left: 0.5rem;
  padding-right: 2rem;
}
.header__form-search::placeholder {
  color: #005DAB;
}
.header__form-btn {
  position: absolute;
  background: transparent;
  right: 0;
  width: 2rem;
}
.header svg path {
  fill: transparent;
  fill: none;
}

.menu__list {
  display: flex;
  justify-content: center;
}
.menu__list-item {
  position: relative;
  width: 100%;
  border-top: 1px solid #005DAB;
  border-bottom: 1px solid #005DAB;
}
.menu__list-item:last-child ul {
  width: 100%;
}
.menu__list-item:last-child ul > li {
  width: 100%;
}
.menu__list-item:first-child ul > li {
  width: 100%;
}
.menu__list-item:hover .menu__sublist {
  display: block;
}
.menu__list-link {
  display: block;
  background: #FDFDFD;
  padding: 1.5rem 1rem;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-align: center;
  font-weight: 500;
  color: #005DAB;
  transition: all 0.3s;
}
.menu__list-link:hover {
  color: #FDFDFD;
  background: #005DAB;
}
.menu__sublist {
  z-index: 10;
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  border: 1px solid #005DAB;
}
.menu__sublist-item {
  width: 40rem;
  background: #FDFDFD;
  transition: all 0.3s;
}
.menu__sublist-item:hover {
  background: #005DAB;
}
.menu__sublist-link {
  display: block;
  padding: 0.5rem 1rem;
  color: #005DAB;
  font-size: 1.3rem;
  text-align: left;
  width: 100%;
}
.menu__sublist-link:hover {
  color: #FDFDFD;
}
.menu__btn {
  display: none;
}

.aside {
  width: 30rem;
  text-align: center;
}
.aside__menu {
  border: 1px solid #005DAB;
  margin-bottom: 2rem;
}
.aside__list-title {
  background: #005DAB;
  padding: 0.5rem 0;
  font-size: 1.6rem;
  color: #FDFDFD;
  font-weight: 400;
  margin-bottom: 2rem;
}
.aside__list-item {
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}
.aside__list-item:hover .aside__sublist {
  display: block;
}
.aside__list-link {
  display: block;
  padding: 0.5rem 0;
  font-size: 1.4rem;
  color: #252525;
  transition: color 0.3s;
}
.aside__list-link:hover {
  color: #005DAB;
}
.aside__sublist {
  display: none;
}
.aside__sublist-item {
  transition: all 0.3s;
}
.main__top {
  display: flex;
  justify-content: space-around;
  margin-bottom: 2rem;
}
.main__list-title {
  font-size: 1.8rem;
  color: #252525;
  font-weight: 500;
  margin-bottom: 2rem;
}
.main__list-item {
  margin-bottom: 1rem;
}
.main__list-link {
  padding: 0.5rem 0;
  font-size: 1.6rem;
  color: #252525;
  transition: color 0.3s;
}
.main__list-link:hover {
  color: #005DAB;
}
.main__preview {
  display: flex;
  justify-content: space-between;
}
.main__img {
  display: block;
  width: 100%;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.5);
}
.main__img-box {
  width: 60rem;
  padding: 0 1.4rem;
}

.footer {
  margin-top: 3rem;
}
.footer__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.footer__menu {
  margin-bottom: 1rem;
}
.footer__list {
  display: flex;
  gap: 2rem;
}
.footer__list-link {
  font-size: 1.3rem;
  color: #005DAB;
  transition: all 0.3s;
}
.footer__list-link:hover {
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #005DAB;
}
.footer__info {
  color: gray;
  font-size: 1.3rem;
  margin-top: 0.5rem;
}

@media (max-width: 768px) {
  .up {
    display: none;
  }

  .header {
    display: none;
  }
  .header .header__lang {
    display: none;
  }

  .hero__inner {
    padding: 1rem 0 10rem;
  }
  .hero__lang {
    display: none;
  }
  .hero__menu-mobile {
    display: block;
  }
  .hero__photo {
    width: 100%;
    min-height: 39rem;
    background: url(/../assets/img/photo.jpg) no-repeat;
    background-position: 60% 40%;
    background-size: 250%;
  }

  .hero__menu {
    order: 1;
  }
  .hero__menu-btn {
    display: block;
    background: transparent;
    position: fixed;
    top: 2rem;
    right: 1rem;
    z-index: 2;
  }
  .hero__menu-btn--active {
    opacity: 0.3;
  }
  .hero__menu-btn-line {
    display: block;
    width: 100%;
    height: 0.2rem;
    background: black;
    position: absolute;
    left: 0;
    transition: all 0.3s linear;
  }
  .hero__menu-btn-line--first {
    top: 0;
  }
  .hero__menu-btn-line--second, .hero__menu-btn-line--third {
    top: 50%;
    transform: translateY(-50%);
  }
  .hero__menu-btn-line--fourth {
    bottom: 0;
    color: black;
  }
  .hero__menu-btn--active .hero__menu-btn-line--first, .hero__menu-btn--active .hero__menu-btn-line--fourth {
    opacity: 0;
  }
  .hero__menu-btn--active .hero__menu-btn-line--second {
    transform: translateY(-50%) rotate(45deg);
  }
  .hero__menu-btn--active .hero__menu-btn-line--third {
    transform: translateY(-50%) rotate(-45deg);
  }
  .hero__menu-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: fixed;
    text-align: center;
    opacity: 0.95;
    top: 0;
    left: 0;
    right: 0;
    padding-bottom: 2rem;
    background-color: #ffffff;
    transform: translatex(80rem);
    transition: all 0.5s;
  }
  .hero__menu-list.hero__menu-list--active {
    z-index: 1;
    transform: translateY(0);
  }
  .hero__menu-list-link {
    display: block;
    font-size: 1.8rem;
    line-height: 2.8rem;
    padding: 2rem 0;
  }

  .certificates__items {
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (max-width: 576px) {
  .hero__photo {
    background-size: 300%;
  }
  .hero__title {
    font-size: 3rem;
    line-height: 3.4rem;
  }

  .skills__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10rem;
  }
}
@media (max-width: 480px) {
  .hero__top {
    display: block;
  }
  .hero__title {
    margin-bottom: 1rem;
  }
  .hero__text {
    text-align: left;
  }
  .hero__photo {
    background-size: 400%;
  }

  .about__items p {
    font-size: 1.6rem;
  }

  .certificates__items {
    width: 90%;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    margin: 0 auto;
  }

  .social__link:hover {
    transform: none;
  }
  .social__link + .social__link {
    margin-left: auto;
  }

  .footer__bottom {
    font-size: 1.6rem;
  }
}
@media (max-width: 320px) {
  .hero__top {
    flex-wrap: wrap;
  }
  .hero__title {
    margin-bottom: 1rem;
  }
  .hero__menu-mobile {
    order: 2;
  }
  .hero__photo {
    background-size: 500%;
  }

  .about__items p {
    font-size: 1.4rem;
  }

  .certificates__items {
    width: 100%;
  }
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  width: 100%;
  padding: 0.5rem !important;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("./ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("./fonts/slick.eot");
  src: url("./fonts/slick.eot?#iefix") format("embedded-opentype"), url("./fonts/slick.woff") format("woff"), url("./fonts/slick.ttf") format("truetype"), url("./fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}
.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}
.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}
.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}
[dir=rtl] .slick-prev {
  left: auto;
  right: -25px;
}
.slick-prev:before {
  content: "←";
}
[dir=rtl] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}
[dir=rtl] .slick-next {
  left: -25px;
  right: auto;
}
.slick-next:before {
  content: "→";
}
[dir=rtl] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}
.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}
.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}
.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}