.main {

  &__inner {
    // background: #acacac;

  }

  &__top {
    display: flex;
    justify-content: space-around;
    margin-bottom: 2rem;
  }

  &__menu {}

  &__list {
    &-title {
      font-size: 1.8rem;
      color: $color1;
      font-weight: 500;
      margin-bottom: 2rem;
      // text-align: center;

    }

    &-item {
      margin-bottom: 1rem;
      // text-align: center;
    }

    &-link {
      padding: 0.5rem 0;
      font-size: 1.6rem;
      color: $color1;

      transition: color .3s;

      &:hover {
        color: $color2;
      }
    }
  }

  &__preview {
    display: flex;
    justify-content: space-between;
    // gap: 1rem;
  }

  &__img {
    display: block;
    width: 100%;
    // height: 100%;
    // border: 1px solid $color2;

    box-shadow: 0px 4px 7px 0px rgba(0,0,0,.5);

    &-box {
      width: 60rem;
      // height: 40rem;
      padding: 0 1.4rem;
    }
  }
}